.main-header__toggle {
  width: 40px;
  height: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 80%;
}
.main-header__toggle:focus,
.main-header__toggle:active {
  opacity: 60%;
}
.main-header__toggle-line {
  height: 2px;
  width: 28px;
  position: absolute;
  background-color: #000;
}
.main-header__toggle--nav-closed .main-header__toggle-line::before {
  content: "";
  height: 2px;
  width: 28px;
  position: absolute;
  top: 11px;
  left: 0;
  background-color: #000;
}
.main-header__toggle--nav-closed .main-header__toggle-line::after {
  content: "";
  height: 2px;
  width: 28px;
  position: absolute;
  bottom: 11px;
  left: 0;
  background-color: #000;
}
.main-header__toggle--nav-opened .main-header__toggle-line {
  transform: rotate(45deg);
}
.main-header__toggle--nav-opened .main-header__toggle-line::after {
  content: "";
  height: 2px;
  width: 28px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(90deg);
  background-color: #000;
}
.main-header__toggle--nojs .main-header__toggle-line {
  display: none;
}
.main-nav {
